
$(()=> {
    const $form = $('.js-form');

    const regex = '\\+7 \\([0-6,9]{1}[0-9]{2}\\) [0-9]{3}-[0-9]{2}-[0-9]{2}'
    
    $("[type='tel']").inputmask({regex});
    
    
    $(".js-form-select").select2({
        language: { noResults: ()=> "Ничего не найдено" }
    })

    const phoneReplace = string => string.replace(/[^\d\+]/g, '');

    const inputChange = (input) => input.on("input", (e)=> $(e.target).parent().removeClass("error") ) 

    $form.on('submit', (e)=> {
        e.preventDefault()
        const target = $(e.target)
        const url = target.attr("action")
        const formInput = target.find("[type='tel']")
        const formCheckbox = target.find("[type='checkbox']")
        const isChecked = !!formCheckbox.length ? formCheckbox.is(":checked") : true
        const button = target.find(".js-form-submit")
        const phone = phoneReplace(formInput.val().slice(1))
        
        inputChange($(e.target).find("input"))

        if(phone.length < 11) {
            formInput.focus()
            formInput.parent().addClass("error")
        }
        if (!isChecked) formCheckbox.parent().addClass("error")
        
        if(phone.length === 11 && isChecked) {
            button.attr("disabled", true)
            $.ajax({
                type: "POST",
                url: url,
                data: { phone: phone },
                success: (response) => {
                    e.target.reset()
                    button.attr("disabled", false)
                    $.magnificPopup.close();
                    $.magnificPopup.open({
                        items: {
                            src: '#modal-success'
                            },
                        type: 'inline',
                        callbacks : {
                            open : function() {
                                // setTimeout(()=> $.magnificPopup.close(), 3000)
                            }
                        }
                    });
                },
                error: (jqXHR, textStatus, errorThrown) => {
                    console.log(textStatus, errorThrown);
                }
            })
        }
    })
})
